body {
    margin: 0;
    padding: 16px;
    ul {
        margin: 0;
        padding: 0;
        li {
            list-style: none;
        }
    }
    h1, h2, h3, h4, h5, h6  {
        margin: 0;
    }
    a {
        text-decoration: none;
    }
    p {
        margin: 0;
    }
    img {
        max-width: 100%;
    }
}